import React from "react";
import styled from "styled-components";
import { Waypoint } from "react-waypoint";
import {
  Link,
  Element,
  animateScroll as scroll,
  scrollSpy,
  scroller
} from "react-scroll";
import { SwipeAction } from "antd-mobile";

const CardWrapper = styled.div`
  border-bottom: 1px solid #e3e3e3;
  ::after {
    content: "";
    display: table;
    clear: both;
  }
`;

const LeftDiv = styled.div`
  float: left;
  width: 80px;
  padding: 10px 20px;
`;

const Date = styled.div`
  color: ${props => (props.weekend ? "#e50505" : "#000000")};
  font-size: 20px;
  width: 30px;
  display: inline-block;
`;

const RightDiv = styled.div`
  float: left;
  padding: 10px 20px;
`;

export default function VcalendarCar(props) {
  return (
    <Element name={props.item.date} className="element">
      <SwipeAction
        style={{ backgroundColor: "gray" }}
        autoClose
        right={[
          {
            text: "Cancel",
            onPress: () => console.log("cancel"),
            style: { backgroundColor: "#ddd", color: "white" }
          },
          {
            text: "Delete",
            onPress: () => console.log("delete"),
            style: { backgroundColor: "#F4333C", color: "white" }
          }
        ]}
        left={[
          {
            text: "Reply",
            onPress: () => console.log("reply"),
            style: { backgroundColor: "#108ee9", color: "white" }
          },
          {
            text: "Cancel",
            onPress: () => console.log("cancel"),
            style: { backgroundColor: "#ddd", color: "white" }
          }
        ]}
        onOpen={() => console.log("global open")}
        onClose={() => console.log("global close")}
      >
        <Waypoint
          onEnter={() => props.waypointEnter(props.item.date)}
          topOffset="80px"
          bottomOffset="80%"
        >
          <CardWrapper>
            <LeftDiv>
              <Date weekend={props.item.weekend}>{props.item.date}</Date> /{" "}
              {props.item.weekofday}
            </LeftDiv>
            <RightDiv>
              근로시간
              <br />
              근로시간
              <br />
              근로시간
              <br />
              근로시간
              <br />
            </RightDiv>
          </CardWrapper>
        </Waypoint>
      </SwipeAction>
    </Element>
  );
}
